import React, { useEffect, useState } from 'react';
import '../styles/aboutMe.css';
import avatar from '../assets/profile_pic.jpeg';

const AboutMe = () => {
  const [greeting, setGreeting] = useState('');

  useEffect(() => {
    const getCurrentTime = () => {
      const currentHour = new Date().getHours();
      if (currentHour >= 5 && currentHour < 12) {
        setGreeting({
          english: 'Good Morning',
          spanish: 'Buenos das',
          vietnamese: 'Cho bui sng',
        });
      } else if (currentHour >= 12 && currentHour < 18) {
        setGreeting({
          english: 'Good Afternoon',
          spanish: 'Buenas tardes',
          vietnamese: 'Cho bui chiu',
        });
      } else {
        setGreeting({
          english: 'Good Evening',
          spanish: 'Buenas noches',
          vietnamese: 'Cho bui ti',
        });
      }
    };

    getCurrentTime();
  }, []);

  return (
    <section className="about-me-section">
      <h2 className="about-me-header">
        <span className="greeting">{greeting.english}</span> ,<span className="greeting">{greeting.spanish}</span> v <span className="greeting">{greeting.vietnamese}</span>
      </h2>
      <div className="about-me-content">
        <img src={avatar} alt="Developer" className="about-me-avatar" />
        <div className="about-me-text">
          <ul>
            <li>
              <span role="img" aria-label="Spanish">
                 Hola, hablo un poco de espaol. Me encanta aprender nuevos idiomas y el espaol es uno de ellos. Disfruto de la msica y las pelculas en espaol, y estoy emocionado de seguir mejorando mis habilidades en este hermoso idioma.
              </span>
            </li>
            <li>
              <span role="img" aria-label="English">
                 Hi, I am very passionate about English. It has been my primary language since childhood, and I have always enjoyed reading and writing in English. It opened doors for me to connect with people from diverse backgrounds.
              </span>
            </li>
            <li>
              <span role="img" aria-label="Vietnamese">
                 Xin cho, ti yu Ting Vit Nam ca ti. Ting Vit l ngn ng m  ca ti, v n  gip ti hiu r vn ha v giao tip vi bn b v gia nh. Ti thc s hnh phc khi ni chuyn bng ting Vit.
              </span>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default AboutMe;
